.SaleClickOn {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.SaleClickOn img {
  width: 32px;
  height: 32px;
}

.SaleClickOn svg {
  width: 100%;
}

.div_user {
  width: 90%;
  display: flex;
  align-items: center;
  gap: 10px;
}

.h2 {
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.span {
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.div_value {
  width: 90%;
  display: flex;
  align-items: center;
  margin-top: 12px;
}

.h4 {
  color: #fff;
  font-family: Roboto;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: flex;
  gap: 16px;
  align-items: center;
}

.span1 {
  color: #a2a1a7;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.div_arrow {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  background-color: #1e251e;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.div_text {
  width: 90%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 6px;
}

.p {
  color: #868894;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.p_1 {
  color: #48b14a;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.div_big {
  width: 90%;
  flex-shrink: 0;
  border-radius: 10px;
  background: #191919;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 22px;
}

.div_1 {
  width: 100%;
  display: flex;
  height: 43px;
  justify-content: space-between;
  align-items: center;
}

.p1 {
  color: #fff;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-left: 12px;
}

.p2 {
  color: #fff;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-right: 26px;
}

.div_2 {
  width: 100%;
  display: flex;
  align-items: center;
  height: 40px;
  justify-content: space-between;
}

.p2_g {
  color: #43a045;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-right: 26px;
}

.div_3 {
  width: 100%;
  display: flex;
  height: 40px;
  justify-content: space-between;
  align-items: center;
}

.div_4 {
  width: 100%;
  display: flex;
  height: 43px;
  justify-content: space-between;
  align-items: center;
}

.right_arrow {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 22px;
}

.btn {
  width: 90%;
  height: 56px;
  flex-shrink: 0;
  background-color: #48b14a;
  color: #fff;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  position: fixed;
  bottom: 0;
}
