.sell {
  display: flex;
  flex-direction: column;
  align-items: center;

  .btns {
    display: flex;
    width: 99%;
    padding: 15px 13px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    button {
      display: flex;
      width: 44.6vw;
      padding: 15px 13px;
      margin-top: 16px;
      justify-content: center;
      align-items: center;
      border: none;
      gap: 10px;
      border-radius: 10px;
      background: #1e251e;
      color: #48b14a;

      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }

  .blockContainer {
    padding: 0 17px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 250px;
    .bigBlock {
      display: flex;
      width: 100%;
      height: 118px;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      gap: 6px;
      flex-shrink: 0;
    }
  }
  .myDeals {
    margin-top: 16px;
    width: 95%;
    display: flex;
    justify-content: space-between;
    padding: 12px;
    select {
      width: 115px;
      color: #48b14a;
      border: none;
      background: transparent;
      outline: none;
      font-size: 14px;
      appearance: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      background-image: url("../../assets/icons/selectIcon.svg");
      background-repeat: no-repeat;
      background-position: right center;
    }
  }
  .showDeals {
    margin-top: 100px;
  }
}
